.section-container{
    display: flex;
}
.section-container .left-side-container{
    width: 50%;
}
.section-container .left-side-container img{
    width: 100%;
    height: 100%;
}


/* Right Side styling */
/* <weight>: Use a value from 300 to 800 */
.section-container .right-side-container{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1vh;
}
.section-container .right-side-container .my-name{
    font-family: "Open Sans", sans-serif;
    font-optical-sizing: auto;
    font-size: 7vh;
    font-weight: 700;
    font-style: normal;
    font-variation-settings:
      "wdth" 100;
    color: #343434;
  
}
.section-container .right-side-container .position-title{
    font-family: "Open Sans", sans-serif;
    font-optical-sizing: auto;
    font-size: 3vh;
    font-weight: 400;
    font-style: normal;
    font-variation-settings:
      "wdth" 100;
}
.section-container .right-side-container .contact-button{
    width: 30vh;
    height: 8vh;
    font-family: "Open Sans", sans-serif;
    font-optical-sizing: auto;
    font-size: 2vh;
    font-weight: 700;
    font-style: normal;
    font-variation-settings:
      "wdth" 100;
    background-color: #343434;
    color : #fff;
    cursor: pointer;

}